export default {
	data() {
		return {
			isMerchant:false,
			isLogin: false,
			//个人信息
			nickName: null,
			avatarUrl: null,
		}
	},
	onShow() {
		var oid = this.getLoginStatus();
		var merchant = this.getMerchantInfo();
		if(merchant){
			this.isMerchant = true;
		}
		if (oid) {
			this.isLogin = true;
			this.nickName = uni.getStorageSync("nickName");
			this.avatarUrl = uni.getStorageSync("avatarUrl");
		}
	},
	methods: {
		toPage(row) {
			switch (row) {
				case "login":
					uni.navigateTo({
						url: "../login/login"
					})
					break;
				case "kefu":
					uni.makePhoneCall({
						phoneNumber: '17689400062'
					});
					break;
				case "edit":
					uni.navigateTo({
						url: "../tool/editInfo"
					})
					break;
				case "bulletin":
					uni.navigateTo({
						url: "../tool/news"
					})
					break;
				case "logout":
					uni.showModal({
						content: '确认退出吗？',
						success: (res) => {
							if (res.confirm) {
								uni.showToast({
									title: "退出成功",
									icon: "none"
								})
								setTimeout(() => {
									uni.removeStorageSync("LOGIN_MERCHANT");
									this.isMerchant = false;
								}, 500)

							}
						}
					});
					break;
			}
		},
		wxGetUserInfo() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					let nickName = infoRes.userInfo.nickName; //昵称
					let avatarUrl = infoRes.userInfo.avatarUrl; //头像
					uni.login({
						provider: 'weixin',
						success: function(loginRes) {
							uni.request({
								url: _this.prefix + "/getJscode2session",
								data: {
									js_code: loginRes.code,
									uname: nickName,
									avatarUrl: avatarUrl,
									appId: "wx4f95fe4f776749b2",
									secret: "ffd17322cb8d52d4096dad2e9078738c",
									loginType: "wxapp"
								},
								success: (res) => {
									let data = res.data
									if (data.code === 200) {
										console.log(data)
										uni.setStorageSync("oid", data.data.openId);
										uni.setStorageSync("nickName", data.data.uname);
										uni.setStorageSync("avatarUrl", data.data.avatarUrl);
										_this.modalName = null;
										_this.successModel = 'Modal';
										setTimeout(() => {
											_this.isLogin = true;
											_this.successModel = null;
											_this.nickName = nickName;
											_this.avatarUrl = avatarUrl;
										}, 500)
									} else {
										uni.showToast({
											title: res.data.msg,
											icon: "none"
										})
									}
								},
							})
						}
					})
				}
			});
		}
	}
}
